import React, { useEffect, useState } from "react";
import ArrEle from "./ArrEle";
import "./App.css";
function App() {
    const [arr, setArr] = useState([]);
    const [i, setI] = useState(0);
    const [j, setJ] = useState(1);
    const [minId, setMinId] = useState(0);
    const [start, setStart] = useState(false);
    useEffect(() => {
        const newArr = [...arr];
        if (start) {
            setTimeout(() => {
                if (i < arr.length - 1) {
                    if (j < arr.length) {
                        if (arr[j] < arr[minId]) {
                            setMinId(j);
                        }
                        setJ(j + 1);
                    } else {
                        let temp = newArr[i];
                        newArr[i] = newArr[minId];
                        newArr[minId] = temp;
                        //bat buoc dung thu tu :D
                        setJ(i + 1);
                        setI(i + 1);
                        setMinId(i + 1);
                        setArr(newArr);
                    }
                }
            }, 4000);
        }
    }, [arr, i, j, start, minId]);
    return (
        <>
            <div style={{ padding: "20px" }}>
                <label>Input array : </label>
                <input
                    type="text"
                    onChange={(e) => setArr(JSON.parse(e.target.value))}
                />
                <button onClick={() => setStart(true)}>Start</button>
                <button onClick={() => setStart(false)}>Stop</button>
            </div>
            <hr />
            <div style={{padding : "50px"}}>
            <div
                className="container"
                style={{border: "2px solid black",paddingLeft : "50px" }}
            >
                {arr.length > 0 && (
                    <>
                        {arr.map((item, index) => (
                            <ArrEle
                                data={item}
                                key={index}
                                targetJ={index === j}
                                targetI={index === i}
                                targetMin={index === minId}
                            />
                        ))}
                        <ArrEle
                            data={0}
                            key={arr.length}
                            targetJ={arr.length === j}
                            targetI={arr.length === i}
                            targetMin={arr.length === minId}
                        />
                    </>
                )}
            </div>
            </div>
            
        </>
    );
}

export default App;
