import React from "react";
function ArrEle({ data, targetJ, targetI,targetMin }) {
    return (
        <div style={{ width: "50px", }}>
            <div
                style={{
                    height: "300px",
                    display: "flex",
                    alignItems: "flex-end",
                }}
            >
                <div
                    className="container"
                    style={{
                        width: "100%",
                        border : "1px solid black",
                        background: targetJ ? "blue" : "yellow",
                        height: `${data * 20}px`,
                        transition: `1s height,2s color`,
                    }}
                ></div>
            </div>
            <div className="pointer" style={{ color :"black",height : "10px",marginTop : 0}}>{data !== 0 && data}</div>
            <div
                className="pointer"
                style={{
                    background: targetI ? "black" : "white",
                }}
            >{targetI && "i"}</div>
            <div
                className="pointer"
                style={{
                    background: targetJ ? "blue" : "white",
                }}
            >{targetJ && "j"}</div>
            <div
                className="pointer"
                style={{
                    background: targetMin ? "red" : "white",
                }}
            >{targetMin && "Min"}</div>
        </div>
    );
}

export default ArrEle;
